<template>
  <v-container class="my-6">
    <v-row>
      <v-col cols="12">
        <form-wizard
          class="custom-form-wizard"
          shape="square"
          title=""
          subtitle=""
          color="#D23F57"
          next-button-text="Continuar"
          finish-button-text="Ordenar"
          back-button-text="Atrs"
          @on-complete="generate()"
        >
          <tab-content title="Carrito" icon="mdi mdi-cart-outline">
            <v-row>
              <v-col cols="12" md="6" lg="8" xl="8">
                <div v-for="(n, index) in getCartProducts" :key="index">
                  <base-card class="d-flex flex-wrap mb-6">
                    <v-img max-width="140" :src="n.img" alt=""></v-img>
                    <div
                      class="d-flex flex-column flex-grow-1 flex-wrap pa-4 mw-0"
                    >
                      <div class="d-flex justify-space-between w-100 mb-3">
                        <div>
                          <div class="d-flex">
                            <h4 class="font-600 text-truncate mb-4">
                              {{ n.nombre }}
                            </h4>
                          </div>
                          <div>
                            <p class="grey--text text--darken-1 mb-0">
                              Bs. {{ n.precio }} x {{n.cantidadInit}}
                              <span class="primary--text ms-2 font-600"
                                >Bs. {{n.precio * n.cantidadInit}}</span
                              >
                            </p>
                          </div>
                        </div>
                        <v-btn icon @click="deleteCartx(n)">
                          <v-icon>mdi-close</v-icon>
                        </v-btn>
                      </div>
                      <div class="d-flex justify-end flex-grow-1 align-end">
                        <div class="d-flex align-center">
                          <v-btn
                          class="rounded"
                            @click="n.cantidadInit>1?removeCartx(n):null"
                            outlined
                            fab
                            x-small
                            tile
                            color="primary">
                            <v-icon>mdi-minus</v-icon>
                          </v-btn>
                          <div class="text-center mx-2">{{n.cantidadInit}}</div>
                          <v-btn
                            class="rounded"
                            @click="addCartx(n)"
                            outlined
                            fab
                            x-small
                            tile
                            color="primary"
                          >
                            <v-icon>mdi-plus</v-icon>
                          </v-btn>
                        </div>
                      </div>
                    </div>
                  </base-card>
                </div>
              </v-col>
              <v-col cols="12" md="6" lg="4" xl="4">
                <base-card>
                  <div class="pa-5">
                    <div class="d-flex justify-space-between">
                      <p class="mb-0 grey--text text--darken-1">Total</p>
                      <h4 class="font-600">Bs. {{cartTotal}}</h4>
                    </div>
                    <v-divider class="my-3"></v-divider>
                    <div class="d-flex align-center mb-4">
                      <h6 class="">Comentario Adicional</h6>
                      <v-chip
                        class="ma-2"
                        label
                        color="primary lighten-5"
                        small
                      >
                        <span class="primary--text text-sm">Nota</span>
                      </v-chip>
                    </div>
                    <v-textarea
                      outlined
                      hide-details=""
                      name="input-7-4"
                      label="Comentario"
                      value=""
                      v-model="details.comment"
                    ></v-textarea>
                    <v-divider class="my-4"></v-divider>
                    <!-- <v-text-field
                      label="Voucher"
                      outlined
                      dense
                      hide-details=""
                      class="mb-4"
                    ></v-text-field>
                    <v-btn
                      color="primary"
                      outlined
                      class="text-capitalize font-600"
                      block
                    >
                      Apply Voucher
                    </v-btn> -->
                    <v-divider class="my-3"></v-divider>
                    <h6 class="mb-4">Estimacin de envio</h6>
                    <p class="text-14 mb-1">Ciudad</p>
                    <v-select
                      dense
                      class="mb-4"
                      :items="items"
                      label="Selecciona la ciudad"
                      outlined
                      hide-details
                      v-model="details.Address"
                    ></v-select>
                    <!-- <v-btn
                      color="primary"
                      outlined
                      class="text-capitalize font-600 mb-4"
                      block
                    >
                      Calculate Shipping
                    </v-btn> -->
                    <!-- <v-btn
                      color="primary"
                      class="text-capitalize font-600 mb-4"
                      block
                    >
                      Checkout Now
                    </v-btn> -->
                  </div>
                </base-card>
              </v-col>
            </v-row>
          </tab-content>
          <tab-content title="Detalles" icon="mdi mdi-newspaper-variant-outline">
            <v-row>
              <v-col cols="12" xl="9" lg="8" md="6">
                <base-card>
                  <div class="pa-5">
                    <h6 class="mb-4">Datos del cliente</h6>

                    <v-row>
                      <v-col cols="12" xl="6" lg="6">
                        <p class="text-14 mb-1">Nombre</p>
                        <v-text-field
                          outlined
                          dense
                          hide-details=""
                          v-model="details.nameClient"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </div>
                </base-card>
              </v-col>
              <v-col cols="12" xl="3" lg="4" md="6">
                <base-card>
                  <div class="pa-5">
                    <div class="d-flex justify-space-between mb-2">
                      <p class="mb-0 grey--text text--darken-1">Total:</p>
                      <h4 class="font-600 mb-0">Bs {{cartTotal}}</h4>
                    </div>
                    <div class="d-flex justify-space-between mb-2">
                      <p class="mb-0 grey--text text--darken-1">Descuento:</p>
                      <h4 class="font-600">-</h4>
                    </div>
                    <v-divider class="my-3"></v-divider>
                    <div class="d-flex justify-end">
                      <h2 class="mb-3">Bs {{cartTotal}}</h2>
                    </div>
                    <v-text-field
                      label="Voucher"
                      outlined
                      dense
                      hide-details=""
                      class="mb-4"
                      v-model="details.voucher"
                    ></v-text-field>
                    <v-btn
                      color="primary"
                      outlined
                      class="text-capitalize font-600"
                      block
                    >
                      Aplicar Voucher
                    </v-btn>
                  </div>
                </base-card>
              </v-col>
            </v-row>
          </tab-content>
        </form-wizard>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapGetters, mapActions  } from 'vuex';
import { FormWizard, TabContent } from "vue-form-wizard";
import "vue-form-wizard/dist/vue-form-wizard.min.css";
export default {
  components: {
    FormWizard,
    TabContent,
  },
    computed: {

    ...mapGetters(['getCartProducts','getMessage']),

    cartTotal() {
      let total = 0;
      this.getCartProducts.forEach(product => {
        total += product.precio * product.cantidadInit
      })
      return total;
      }
    },
  data() {
    return {
      items: ["La Paz", "Cochabamba", "Santa Cruz", "Oruro"],
      radios: null,
      radiosTwo: null,
      details:{
        nameClient:"",
        comment:"",
        Address:"",
        voucher:""
      },
    };
  },
  methods:{
    ...mapActions([ "addCart", "removeCart", "deleteCart", "addDetailsMessage","removeAll" ]),
    removeCartx (item) {
      this.removeCart(item)
    },
    deleteCartx (item) {
      this.deleteCart(item)
    },
    addCartx (item) {
      this.addCart(item);
    },
    generate(){
      this.addDetailsMessage(this.details)
      this.removeAll()
      this.$router.push("/");
      window.open(`https://web.whatsapp.com/send?phone=59168004995&text=${this.getMessage}`, "_blank");
    }
  }
};
</script>
